//mutations
const mutations = {
    //替换questionItems
    replaceQuestionItems(state,{item}){
        state.questionItems = item;
    },
    //增加按钮
    add(state,{id1,id2,sign}){//sign=0为单选，为1为多选
       if(sign == 0){
           state.Paper.options[id1-1].optionsubs[id2-1].single++;
        }
        else{
           state.Paper.options[id1 - 1].optionsubs[id2 - 1].multiple++;
        }
    },
    //减按钮
    substract(state,{id1,id2,sign}){
        if (sign == 0) {
            state.Paper.options[id1 - 1].optionsubs[id2 - 1].single--;
        }
        else {
            state.Paper.options[id1 - 1].optionsubs[id2 - 1].multiple--;
        }
    },
    handleSubmitAndPreview( state, { type }) {//type为本次提交的试卷类型[单选0，多选1，单选与多选2]
        //准备部分，准备要发送的内容即Trans
        var i = 0;
        var j = 0;
        state.Trans.Blocks = [];//刷新blocks
        if(type == 0 ||type == 2){
            for (i = 0; i < state.Paper.options.length; i++) {
                for (j = 0; j < state.Paper.options[i].length; j++) {
                    if (state.Paper.options[i].optionsubs[j].single != 0) {
                        state.Trans.Blocks.push({
                            mainTitle: state.options[i].value,
                            subTitle: state.options[i].optionsubs[j].value,
                            difficulty: 1,
                            questionAmount: state.options[i].optionsubs[j].single,
                            isMulti: 0,
                        });
                    }
                }
            }
        }
        if(type == 1 || type == 2){
            for (i = 0; i < state.Paper.options.length; i++) {
                for (j = 0; j < state.Paper.options[i].length; j++) {
                    if (state.Paper.options[i].optionsubs[j].multiple != 0) {
                        state.Trans.Blocks.push({
                            MainTitle: state.options[i].value,
                            SubTitle: state.options[i].optionsubs[j].value,
                            Difficulty: 1,
                            QuestionAmount: state.options[i].optionsubs[j].multiple,
                            IsMultiple: 1,
                        });
                    }
                }
            }
        }
        router.push({
            path:'/preview',
        });
        //数据发送部分
        axios.post('/#',{
           WholeBlockAmount:state.Trans.WholeBlockAmount,
           Blocks:state.Trans.Blocks,

        })
        .then(function(response){
            //这个地方要加强测试，很可能无法操作
            state.questionItems = response.data;
        })
        .catch(function(error){

        })

    },
    
    clean(state,{id1,id2}){

    },
}
//getters
const getters = {
   SingleChoiceAmount:(state) =>{
       var i = 0;
       var j = 0;
       var sum = 0;
       for(i=0;i<5;i++){
           state.Paper.options[i].single = 0;
       }
       for(i=0;i<5;i++){
           for (j = 0; j < state.Paper.options[i].optionsubs.length;j++){
               state.Paper.options[i].single += state.Paper.options[i].optionsubs[j].single;
           }
       }
       for(i=0;i<5;i++){
           sum += state.Paper.options[i].single;
       }
        state.Paper.SingleChoiceAmount = sum;
       return sum;
   },
    MultipleChoiceAmount: (state) => {
        var i = 0;
        var j = 0;
        var sum = 0;
        for (i = 0; i < 5; i++) {
            state.Paper.options[i].multiple = 0;
        }
        for (i = 0; i < 5; i++) {
            for (j = 0; j < state.Paper.options[i].optionsubs.length; j++) {
                state.Paper.options[i].multiple += state.Paper.options[i].optionsubs[j].multiple;
            }
        }
        for (i = 0; i < 5; i++) {
            sum += state.Paper.options[i].multiple;
        }
        state.Paper.MultipleChoiceAmount = sum;
        return sum;
    },
    SingleChoicePoint:(state) =>{
        if (state.Paper.WholePointSingle / state.Paper.SingleChoiceAmountFull > 0 && state.Paper.WholePointSingle / state.Paper.SingleChoiceAmountFull<100){
            return state.Paper.WholePointSingle / state.Paper.SingleChoiceAmountFull;
        }
        else{
            return '';
        }       
   },
    MultipleChoicePoint: (state) => {
        if (state.Paper.WholePointMultiple / state.Paper.MultipleChoiceAmountFull > 0 && state.Paper.WholePointMultiple / state.Paper.MultipleChoiceAmountFull < 100) {
            return state.Paper.WholePointMultiple / state.Paper.MultipleChoiceAmountFull;
        }
        else {
            return '';
        }
    }
}

export default {
    namespaced:true,
    state,
    mutations,
    getters,
}