//与人工组卷绑定的状态库
const state = {
    TargetAmount:0,
    SelectedAmount:0,
    questionItems: [//选择框
        // {
        //     id:
        //     mainTitle:
        //     subTitle:
        //     content:
        //     options:
        //     answer:
        //     difficulty:
        //     ismulti:
        // }
    ],
    questionSheets:[//答案框

    ]
}
//mutations
const mutations = {
    set(state, datas) {//更新试题库
        state.questionItems = datas;
    },
    reset(state) {//清空试题库
        state.questionItems = [];
    },
    add(state,{item}){//将题目加入到自选题目列表中
        let obj = state.questionSheets.find( a =>{
            item.id === a.id;
        });
        if(obj){
            //已选的试题库中有一样的
        }
        else{
            state.questionSheets.push(item);
        }
        
    },
    questionDelete(state,{item}){
        state.questionSheets.remove(item);
    },
    handleSubmit(state){
        var s = [];
        var i = 0;
        for(i = 0;i<state.questionItems.length;i++){
            s.push(state.questionItems[i].id)
        }
        this.axios.post('/#',{
            data:s,
        })
        .then(function (response) {
            //这个地方要加强测试，很可能无法操作
            //准备下载    
        })
        .catch(function (error) {

        })


    }
}
export default {
    namespaced: true,
    state,
    mutations,
    // getters,
}