import Vue from 'vue'
import VueRouter from 'vue-router'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: {
      name: 'Home',
    },
  },
  {
    path: '/preview',
    name: 'Preview',
    component: () => import('../views/Preview.vue'),
  },
  {
    path:'/test',
    name:'Test',
    component:() =>import('../views/Test.vue')
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('../views/Home.vue'),
  },
  {
    path:'/login',
    name:'login',
    component: () => import('@/components/Login'),
  },
  {
    path: '/papermake',
    name: 'papermake',
    component: () => import('@/components/PaperMake'),
  },
  {
    path: '/papermakerandom',
    name: 'papermakerandom',
    component: () => import('@/components/PaperMakeRandom'),
  },
  {
    path: '/papermakecustom',
    name: 'papermakecustom',
    component: () => import('@/components/PaperMakeCustom'),
  },
  {
    path: '/questioninput',
    name: 'questioninput',
    component: () => import('@/components/QuestionInput'),
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.path === '/login') {
    next();
  }
  else if (sessionStorage.getItem("isLogin") === "true") {
    next();
  }
  else {
    let currentPath = to.fullPath;
    router.push({ name: 'login', query: { redirect: currentPath } });
  }
})


export default router;
